import React from "react";
import {addPost_action_creator, } from "../../../redux/Profile_reducer";
import Posts from "./posts";
import {connect} from "react-redux";

const MapStateToProps=(state)=> {
    return{
        post_data:state.profile_page.post_data,
    }
}
const MapDispatchToProps=(dispatch)=> {
    return{
        addPost:(add_post_form_text)=>{
            dispatch(addPost_action_creator(add_post_form_text))
        }
    }
}
const Posts_conteyner= connect (MapStateToProps,MapDispatchToProps)(Posts)
export default Posts_conteyner




















