import React, {useState} from "react";
import clas from "./Profile_info.module.css"
import Preloader from "../../Preloader/Preloader/Preloader";
import Profile_status_with_hoock from "./Profile_status/Profile_statusHUKS";
import letUserFoto from "../../../acets/images/Foto/standatr_foto_ForAva.png"
import User_description_Owner from "./User_description_Owner";

const Profile_info = ({profile, isOwner, status, Put_status_T, onUpload, uploadProfile,}) => {
    let [isEditMode, SetEditMode] = useState(false)
    if (!profile) {
        return <div><Preloader/> profile in the loading</div>
    }
    let dounloudFoto = (e) => {
        if (e.target.files[0]) {
            onUpload(e.target.files[0])
        }
    }
    const onSubmit =  (formData) => {
         uploadProfile(formData).then(
             ()=>{
                 SetEditMode(false)
             }
         )
    }
    return <div>
        <div className={clas.AllUperPart}>
            <div>
                <div className={clas.ava}>
                    {/**here be a foto of proofile_Page**/}
                    <img className={clas.arbat} src={profile.photos.large || letUserFoto} alt=""/><br/>
                    {isOwner && <input className={clas.uploadFile} type={'file'} onChange={dounloudFoto}/>}
                </div>
                <span className={clas.status}>STATUS:</span><Profile_status_with_hoock status={status} Put_status_T={Put_status_T}/>
                {isEditMode
                    ? <User_description_Owner
                        initialValues={profile}
                        profile={profile} isOwner={isOwner}
                        SetEditMode={SetEditMode}
                        fromEditMode={() => {
                            SetEditMode(false)
                        }}
                        onSubmit={onSubmit}

                    />
                    : <User_description profile={profile} isOwner={isOwner}
                                        toEditMode={() => {
                                            SetEditMode(true)
                                        }}/>
                }
            </div>
        </div>


    </div>
}

const User_description = ({profile, isOwner, toEditMode,}) => {

    return <form>
        <div>
            {isOwner &&
            <b className={clas.chMode} onClick={toEditMode}> chande edit mode </b>}
        </div >
        <div className={clas.uperFoled} >
            <div  >
                {"FULL NAME:       "}<span >{profile.fullName ? profile.fullName : "______"}</span>
            </div>
            <div>
                {"looking job: "}<span>{profile.lookingForAJob ? 'yes ' : " ______"}</span>
            </div>
            <div>
                {"My CV: "}<span>{profile.lookingForAJobDescription ? profile.lookingForAJobDescription : "______"}</span>
            </div>
            <div>
                {" aboutMe "}<span>{profile.aboutMe ? profile.aboutMe : "______"}</span>
            </div>
        </div>


        <div className={clas.SM_Main}>
            {/**CONTACTS    **/}
            <b>{"my contacts"} </b>
            {
                Object.keys(profile.contacts).map(key => {

                    return <Contact key={key} contactTitle={key}
                                    profile={profile}
                                    contactValue={profile.contacts[key]}
                    />
                })}
        </div>
    </form>
}
const Contact = ({contactTitle, contactValue}) => {

    return <div className={clas.SM}>
        {<b className={clas.SM1}>{contactTitle}</b>} :
        {<span>{contactValue || '  - - - - -'}</span>}
    </div>
}


export default Profile_info

