import React from "react";
import Profile_info from "./Profile_info/Profile_info";
import Posts_conteyner from "./myposts/posts_conteiner";
import clas from './Profile_page.module.css'
const Profile_page = (props)=> {
    return(
        <main className={clas.fullProfilePage}>
            <Profile_info status={props.status}
                          profile={props.profile}
                          Put_status_T={props.Put_status_T}
                          onUpload={props.onUpload}
                          isOwner = {props.isOwner}
                          uploadProfile={props.uploadProfile}
            />
            <Posts_conteyner
            />
        </main>);
};
export default Profile_page