import React, {useEffect, useState} from "react";
///import Status_text from "./Profile_status_text";

const Profile_status_with_hoock = (props:any) => {

    //let statewithState = useState(false)
    //let editMode = statewithState[0]
    //let set_editMode = statewithState[1]
    let [editMode, setEditMode] = useState(false)
    let [status, setStatus]= useState(props.status)

     useEffect( ()=>{setStatus(props.status)},[props.status])

    let activate_edit_mode = () =>{setEditMode(true)}
    let deactivate_edit_mode = () =>{
        setEditMode(false)
       props.Put_status_T(status)
    }
    let onStatusChange = (e:any) =>{setStatus(e.currentTarget.value)}
    return <div>
        {!editMode &&
        <div>
            <span onClick={activate_edit_mode}> {props.status || '--------'} </span>
        </div>
        }
        {editMode &&
        <div>

            <input autoFocus={true}
                   onChange={onStatusChange}
                   value={status} />
        <button onClick={deactivate_edit_mode}> вдправити</button>
        </div>

        }

    </div>
}

export default Profile_status_with_hoock


