import React from "react";
import styles from "./form_control.module.css"
import {Field} from "redux-form";

const FormControl = ({input, meta:{touched,error}, children, ...props}) => {

    const haveError = touched && error;

    return <div>
        <div className={styles.formControl + " " + (haveError ? styles.error : '')}>
            <div>
                <div>
                    {children}
                </div>
                {haveError && <span>{error}</span>}
            </div>

        </div>
    </div>
}


export const Textarea = (props) => {
    const {input, meta, child, ...restProps} = props;
    return <FormControl {...props}><textarea {...input} {...restProps} /></FormControl>

}

export const Input = (props) => {
    const {input, meta, child, ...restProps} = props;
    return <FormControl {...props}><input {...input} {...restProps} /></FormControl>
}

export const CreateField = (name, placeholder, validate, component,
                            type='textarea',props = {}, text = '') => {
    //const maxLength100 = max_letngtcreator(40)
    return (
        <div>
            <Field name={name} placeholder={placeholder}
                   validate={validate} component={component} type={type}/> {text}
        </div>
    )
}










