import React from "react";
import classes from "./posts.module.css"
import Post from "./post/post";
import {Field, reduxForm} from "redux-form";
import {max_letngtcreator, required} from "../../utils/validators/validators";
import {Textarea} from "../../form_controls/form_controls";


const Posts = React.memo((props) => {
    console.log("yuoooooo")
    let post_item =
        props.post_data.map((post_1) => <Post key={post_1.id}  text={post_1.masage_text} id={post_1.id} name={post_1.name}
                                              src={post_1.src} like_count={post_1.like_count}/>);

    let addPost_form=(value)=>{
        props.addPost(value.add_post_form_text)
    }
    return (
        <main className={classes.main}>
            <Post_form_reduxform onSubmit={addPost_form}/>
            <div className={classes.item}>{post_item}</div>
        </main>);
})

const maxlength2=max_letngtcreator(10)
const Post_form = (props) => {
    return <div>
        <form action="" onSubmit={props.handleSubmit}>
                <h3> My post</h3>
                <label>
                <Field  placeholder="введи текст" name={'add_post_form_text'}
                        component={Textarea} validate={[required,maxlength2]} />
                    <button>add</button>
                </label>
        </form>
    </div>
}
const Post_form_reduxform=reduxForm({form:'post_write'})(Post_form)
export default Posts






















