import React from "react";
import classes from "./Profile_info.module.css";
import {required} from "../../utils/validators/validators";
import {CreateField, Input} from "../../form_controls/form_controls";
import {reduxForm} from "redux-form";
import clas from "../../form_controls/form_control.module.css";

const User_description_Owner = ({isOwner, fromEditMode, handleSubmit, profile,error}) => {

    return <form onSubmit={handleSubmit}>
        {error && <div className={clas.formSumError}><h4> {error}</h4></div>}

        <div>
            {isOwner && <div>
            </div>}
            <b className={clas.chMode} onClick={fromEditMode}> chande information</b>
        </div>
        <div className={clas.uperFoled}>
            {CreateField("fullName", 'fullName', [required], Input)}
            {CreateField("aboutMe", 'AboutMe', [required], Input)}
            {CreateField("lookingForAJobDescription", 'looking job(CV)', [required], Input)}
            <button> safe1111</button>
        </div>
        <div className={classes.SM_Main}>
            {
                Object.keys(profile.contacts).map(key => {
                    return <div>
                    <b key={key}>{key}{CreateField("contacts."+key, key,
                        [], Input,)}
                    </b>
                    </div>
                })
            }

        </div>
    </form>
}

const User_description_OwnerReduxform = reduxForm({form: 'userProfile',})
(User_description_Owner)

export default User_description_OwnerReduxform

