import React from "react";
import Profile_page from "./Profile_page";
import {connect} from "react-redux";
import {get_status_T, get_user_T, onUpload, Put_status_T, uploadProfile} from "../../redux/Profile_reducer";
import {withRouter} from "react-router-dom";
import {compose} from "redux";



class Profile_pageC extends React.Component {

    refreshPage() {
        let userId = this.props.match.params.userId
        if (!userId) {
            userId = this.props.autorized_user_id;
            if (!userId) {
                this.props.history.push('/login')
            }
        }
        this.props.get_user_T(userId)
        this.props.get_status_T(userId)
    }

    componentDidMount() {
        this.refreshPage()
    }


    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.match.params.userId !== this.props.match.params.userId) {
            this.refreshPage()

        }
        if(prevProps.profile !==this.props.profile ){

            this.refreshPage()
        }
    }


    render() {
        return (
            <Profile_page
                {...this.props}
                isOwner = {!this.props.match.params.userId}
                          status={this.props.status}
                          Put_status_T={this.props.Put_status_T}
                onUpload={this.props.onUpload}
            />
        )

    }

}

let MapStateToProps = (state) => {
    return {
        profile: state.profile_page.profile,
        status: state.profile_page.status,
        autorized_user_id: state.auth.userId,
        autorized_isOuth: state.auth.isOuth,
        isOwner:state.profile_page.isOwner,
    }
}

export default compose(
    connect(MapStateToProps, {get_user_T, get_status_T,
        uploadProfile,Put_status_T,onUpload}),
    withRouter,
    //With_outh_redirect
)
(Profile_pageC)


